body{
	height: 100%;
	width: 100%;
	margin: auto 0;
}
/*HELLO SECTION*/
.container-hello{
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  height: 100vh;
  background: linear-gradient(148deg, rgb(64, 64, 64), rgb(92, 92, 92));
}
.hello{
  color: white;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  font-family: 'Kaushan Script', cursive;
  font-weight:400;
  font-size: 2.5em;

}
.button-hello {
  border-radius: 6px;
  background-color: #000000;
  border: none;
  color: #FFFFFF;
  text-align: center;
  font-size: 26px;
  padding: 10px;
  width: 185px;
  transition: all 0.5s;
  cursor: pointer;
  margin: 6px;
}
.button-hello span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}
.button-hello span:after {
  content: '\00bb';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}
.button-hello:hover span {
  padding-right: 28px;
}
.button-hello:hover span:after {
  opacity: 1;
  right: 0;
}
@media only screen and (max-width: 900px){
    .hello{
    font-size: 0.9em;
    padding: 0;
    }
    .button-hello{
    font-size: 1.8em;
    padding: 1px;
    
    }
  }
