/*ABOUT SECTION*/
.container-about{
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  height: 100vh;
background: linear-gradient(0deg, rgb(255, 255, 255), rgb(39, 37, 37));
}
.proimg{
    display: block;
    position: relative;
    border-radius: 1rem;
    width: 250px;
    height: auto;
    border-style: solid;
    margin-left: auto;
    margin-right: auto;
    top: 40px; 
    z-index: 1;
}
.aboutcard{
display: flex;
flex: 1 0 0%;
position: relative;
flex-direction: column;
align-items: center; 
min-width: 0;
justify-content: space-around;
max-width: 550px; 
margin-top: 0px;
margin-bottom: 20px;
margin-left: auto;
margin-right: auto;
word-wrap: break-word;
background-clip: border-box;
border: 1px solid white;
border-radius: .25rem;
font-family: var(--bs-font-sans-serif);
background: linear-gradient(250deg, rgb(255, 255, 255), rgb(231, 228, 228));    max-height: 500px;
padding: 80px 20px 20px 20px;
padding-top: 80px;
}

.about-body {
    flex: 1 1 auto;
    padding: 1rem 1rem;
    max-width: 600px;
    margin-top: 0px;
    height:auto;
}
@media only screen and (max-width: 600px){
  .aboutcard,.about-body{
    font-size: 0.8em;
    padding: 0;
    }
  }



