.grid-wrap{
  display: grid;
  grid-gap: 12px ;
  grid-template-columns: repeat(auto-fill,minmax(290px,1fr));
  grid-template-rows: 1fr;
  align-content: center;
}
.item-a {
  grid-column-start: 2;
  grid-column-end: five;
  grid-row-start: row1-start;
  grid-row-end: 3;
}
